#wirkungskette {
    position: relative;
    margin: 50px 0px 150px 0px;
    svg {
        display: block;
        margin: 0 10px;
        overflow: visible;
        g#path > polygon {
            opacity: 0; }
        image {
            display: block;
            position: relative; }
        @include breakpoint(large) {
            margin: 0 100px; }
        @include breakpoint(xlarge) {
            margin: 0 208px; } }
    button {
        color: white;
        margin: 20px 0 0 0; } }

.wk-text-box {
    position: absolute;
    width: 60vw;
    @include breakpoint(xlarge) {
        width: 600px; } }

.wk-text-box-1 {
    top: -2%;
    left: 6%; }
.wk-text-box-2 {
    top: 18%;
    right: 0px;
    text-align: right; }
.wk-text-box-3 {
    top: 34%;
    left: 8%; }
.wk-text-box-4 {
    top: 49%;
    left: 19%; }
.wk-text-box-5 {
    top: 70%;
    right: 17%;
    text-align: right; }
.wk-text-box-6 {
    top: 89%;
    left: 6%; }





