/////////////////////////
//  Grid
/////////////////////////

.container {
    display: flex;
    flex-direction: column; }

.row {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    max-width: $site-width;

    &.collapse {
        margin-left: 0 !important;
        margin-right: 0 !important;
        >.column,
        >.columns {
            padding-left: 0 !important;
            padding-right: 0 !important; } } }

.column,
.columns {
    min-width: initial;
    padding-left: $column-small-gap;
    padding-right: $column-small-gap;
    @include breakpoint(medium) {
        padding-left: $column-gap;
        padding-right: $column-gap; } }


// Collapse column to the grid by the breakpoints
@each $size, $value in $breakpoints {
    @media screen and (max-width: $value) {
        .row.collapse-#{$size} {
            //margin: 0 !important
            >.column,
            >.columns {
                margin-left: 0 !important;
                padding-left: 0 !important; } } } }


// Generate Column Classes
@each $size, $value in $breakpoints {
    .js-bp-#{$size} { //classes to pass breakpoints to js
        width: $value; }
    @include breakpoint($value) {
        @for $i from 0 through $grid-columns {
            .#{$size}-#{$i} {
                flex: 0 0 100% / $grid-columns * $i;
                max-width: 100% / $grid-columns * $i; }

            .#{$size}-push-#{$i} {
                margin-left: 100% / $grid-columns * $i; }

            .#{$size}-pull-#{$i} {
                margin-right: 100% / $grid-columns * $i; }

            .#{$size}-left {
                text-align: left; } } } }



// Positioning Class for Content Rows
.content-right {
    justify-content: flex-end; }

.content-bottom {
    align-items: flex-end; }

.content-verti {
    align-items: center; }

.content-hori {
    justify-content: center; }

.content-middle {
    justify-content: center;
    align-items: center; }


// Positioning Class for Columns
.self-top {
    align-self: flex-start; }

.self-bottom {
    align-self: flex-end; }

.self-center {
    align-self: center; }


.only-mobile {
    @include breakpoint(medium) {
        display: none !important; } }

.only-desktop {
    @include breakpointDown(medium) {
        display: none !important; } }

.hidden {
    display: none; }
